import classes from './OTCtrades.module.css';
import { useState} from 'react';
import './OTCtrades.css';

const OTCtrades = () => {
    const [toggleFormState, setIsToggleFormState] = useState(1);
    const [inputEmail, setInputEmail] = useState("");
    const [inputCrypto,setInputCrypto] = useState("");
    const [inputAmount,setInputAmount] = useState("");
    const [inputNotes,setInputNotes] = useState("");



    const [inputEmailSell, setInputEmailSell] = useState("");
    const [inputCryptoSell,setInputCryptoSell] = useState("");
    const [inputAmountSell,setInputAmountSell] = useState("");
    const [inputNotesSell,setInputNotesSell] = useState("");
 

    const toggleFormTab = (index) => {
        setIsToggleFormState(index);
    };
    const emailInputHandler =(event) => {
        setInputEmail(event.target.value);
    }
    const cryptoInputHandler = (event) => {
        setInputCrypto(event.target.value);
    }

    const amountHandler = (event) => {
        setInputAmount(event.target.value);
    }

    const notesHandler = (event) => {
        setInputNotes(event.target.value);
    }



    
    const emailInputSellHandler =(event) => {
        setInputEmailSell(event.target.value);
    }
    const cryptoInputSellHandler = (event) => {
        setInputCryptoSell(event.target.value);
    }

    const amountSellHandler = (event) => {
        setInputAmountSell(event.target.value);
    }

    const notesSellHandler = (event) => {
        setInputNotesSell(event.target.value);
    }

    const formSubmission = (event) => {
        event.preventDefault();
        console.log(inputEmail);
        console.log(inputCrypto)
        console.log(inputAmount);
        console.log(inputNotes);
    }

    const sellFormSubmission = (event) => {
        event.preventDefault();
        console.log(inputEmailSell);
        console.log(inputCryptoSell)
        console.log(inputAmountSell);
        console.log(inputNotesSell);
    }

    return(
        <div className={classes.OTCtradesTop}>
            <h1>OTC Services</h1>
            <div className={classes.otcTradesTop}>
                <h2>Request for Quote</h2>
                <div className={classes.requstForm}>
                     <div className={classes.row}>
                                <div  className={toggleFormState === 1 ? "formTab active-formTab" : "formTab"}
                                onClick={() => {
                                    toggleFormTab(1);
                                }}
                                >
                                    I want to buy
                                </div>
                                <div  className={toggleFormState === 2 ? "formTab active-formTab" : "formTab"}
                                onClick={() => {
                                    toggleFormTab(2);
                                }}
                                >
                                    I want to sell
                                </div>
                      </div>

                      <div
                         className={toggleFormState === 1 ? "formContent active-form" : "formContent"}>
                         <form onSubmit={formSubmission}>
                             <input id='email'
                              value={inputEmail}
                              onChange={emailInputHandler}
                             type="text" placeholder='Email...'/>
                             <div className='formRow'>
                                 <input 
                                      type='text'
                                      value={inputCrypto}
                                     onChange={cryptoInputHandler}
                                         placeholder='Cryptocurrecny'/>
                                 <input type='number' onChange={amountHandler} value={inputAmount}  placeholder='Amount' />
                             </div>
                             <input id='notes' onChange={notesHandler} value={inputNotes} type='text' placeholder='Notes'/>
                            <input type="submit" className='buy__btn' value="Buy" />
                         </form>
                      </div>
                      <div
                         className={toggleFormState === 2 ? "formContent active-form" : "formContent"}>
                         <form onSubmit={sellFormSubmission}>
                             <input id='email'
                              value={inputEmailSell}
                              onChange={emailInputSellHandler}
                             type="text" placeholder='Email...'/>
                             <div className='formRow'>
                                 <input 
                                      type='text'
                                      value={inputCryptoSell}
                                     onChange={cryptoInputSellHandler}
                                         placeholder='Cryptocurrecny'/>
                                 <input type='number' onChange={amountSellHandler} value={inputAmountSell}  placeholder='Amount' />
                             </div>
                             <input id='notes' onChange={notesSellHandler} value={inputNotesSell} type='text' placeholder='Notes'/>
                            <input type="submit" className='buy__btn' value="Sell" />
                         </form>
                      </div>
                </div>
            </div>
        </div>
    )
}
export default OTCtrades;