import { useState } from 'react';
import classes from './Swap.module.css';


const Swap = () => {
    const [chooseCurrency,setIsShooseCurrency] = useState("");
    const [showShuffle,setIsShowShuffle] = useState(false);
    const [showTransfer,setIsShowTransfer] = useState(false);

    const chooseCurrencyHandler = (event) => {
        setIsShooseCurrency(event.terget.value);
    }

    const showShuffleHandler = () => {
        setIsShowShuffle(true);
    }

    const hideShuufleHandler = () => {
        setIsShowShuffle(false);
    }

  const showTransferHandler = () => {
    setIsShowTransfer(!showTransfer);
  }

     return(
        <div className={classes.SwapTop}>
             <div className={classes.swapInner}>
                 {showTransfer && <div className={classes.transferCrypto}>
                     <p>Transfer</p>
                     <div className={classes.seletCrypto}>
                         <img src='./img/filDown.svg' alt='Fill Down' />
                     </div>
                     <input  type='number' placeholder='0'/>
                     <h3>To</h3>
                     <input type='text' placeholder="Recipent Address" />
                     <input type='submit'  value='Connect' />
                 </div>}
                 <div className={classes.headerTop}>
                    <div className={classes.connnecBtn} onClick={showTransferHandler}>
                        <h3>TRANSFER</h3>
                        <img src='./img/swap1.svg' alt='Swap' />
                    </div>
                    <div className={classes.shuffle}>
                        <img src='./img/refresh.svg' alt='Refresh'/>
                        <img src='./img/shuffle.svg' onClick={showShuffleHandler} alt='Shuffle'/>
                    </div>
                 </div>
                 <div className={classes.ChooseFrom}>
                       <p>Choose</p>
                       <h2>FROM</h2>
                       <div className={classes.chooseCoin}>
                           <div className={classes.chooseCurrency}>
                                <span>
                                    {/* <img>Show flag here</img> */}
                                </span>
                                <img src='./img/filDown.svg' alt='Fill Down'/>
                           </div>
                           <input type="number" onChange={chooseCurrencyHandler} value={chooseCurrency} placeholder="0" />
                       </div>
                       <p>Balance:</p>
                       <h2>0</h2>
                       <img src='./img/fillblack.svg' className={classes.fillDown} alt='Fill Down'/>
                 </div>
                 <div className={classes.estimate}>
                   <h2>To [ Estimated ]</h2>
                    <div className={classes.row}>
                    <div className={classes.estimateCurrency}>
                         <span>
                            {/* <img>Show flag here</img> */}
                        </span>
                        <img src='./img/filDown.svg' alt='Fill Down'/>
                   </div>
                   <div className={classes.estimatedCoin}>0</div>
    
                    </div>     

                 </div>
              <h2><span style={{color:"rgba(30,136,229,1)"}}>ETH</span> UniswapV2Router02</h2>
              <button className={classes.connectBtn}>Connect</button>
             </div>
             {showShuffle && <div className={classes.shuffleBackdrop} onClick={hideShuufleHandler}>
                 <div className={classes.shuffleInner}>
                     <h2>Switch Network</h2>
                     <p>On your Metamast Wallet to :</p>
                     <div className={classes.switchImg}>
                         <div className={classes.binace}>
                           <img src='./img/circleb.png' alt='Binace log' />
                           <h2>Binace</h2>
                           <small>(BNC)</small>
                         </div>
                         <div className={classes.ethereum}>
                           <img src='./img/circleeth.png' alt='Binace log' />
                           <h2>Ethereum</h2>
                         </div>
                     </div>
                 </div>
             </div>}
        </div>
    )
}
export default Swap;