import classes from './Home.module.css';
import Swap from '../Swap/Swap';
import OTCtrades from '../OTCTraders/OTCtrader';
import { useState } from 'react';
import Manage from '../Manage/Manage';
import HodlVault from '../Hodl/HodlVault';


const Home = () => {
    const [showSwap, setIsShowSwap] = useState(false);
    const [showOTCtrades,setIsShowOTCtrades] = useState(false);
    const [showManage,setIsShowManage] = useState(false);
    const [showHodlVault,setIsShowHodlVault] = useState(false);
    const [showHome,setIsShowHome] = useState(true);


    const swapShowHandler = () => {
        setIsShowSwap(true)
        setIsShowHome(false);
    }

    const moveBackHandler = () => {
        setIsShowSwap(false);
        setIsShowOTCtrades(false);
        setIsShowHome(true);
        setIsShowManage(false);
        setIsShowHodlVault(false);
    }

    const showOTCtradesHandler = () => {
        setIsShowOTCtrades(true);
        setIsShowHome(false);
    }


    const manageShowHandler = () => {
        setIsShowManage(true);
        setIsShowHome(false);
    }

    const hodlValutHandler = () => {
        setIsShowHodlVault(true);
        setIsShowHome(false);
    }



    return (
        <div className={classes.homeTop}>
            {showHome && <>
                <button className={classes['connect__btn']}>Connect</button>
            <div className={classes.row}>
                <div className={classes.card} onClick={swapShowHandler}>
                    <h2>Swap</h2>
                    <p>Swap any Token on different <br/> Chains with the best rates</p>
                    <img src='./img/swipe.svg' style={{width:"90%",marginTop:"20px"}} alt='Swap'/>
                </div>
                <div className={classes.card} onClick={showOTCtradesHandler}>
                    <h2>OTC service</h2>
                    <p>Trade amounts through <br/> Orangswap OTC service</p>
                    <img style={{width:"90%"}} src='./img/service.svg' alt='OTC'/>
                </div>
                <div className={classes.card} onClick={manageShowHandler}>
                    <h2>Manage Your Profile</h2>
                    <p>Manage your profile in real <br/> time and keep an overview</p>
                    <img src='./img/manage1.svg' style={{width:"90%",marginTop:'55px'}} alt='OTC'/>
                </div>
                <div className={classes.card} onClick={hodlValutHandler}>
                    <h2>HODL Vault</h2>
                    <p>Time Locked Vault</p>
                    <img style={{marginTop:"75px",width:'90%'}} src='./img/hodel.svg' alt='OTC'/>
                </div>
            </div>

            </> }
            {showSwap && <div>
                <button className={classes.moveBack} onClick={moveBackHandler}> <img src='./img/direction.svg' alt='direction'/>  Back</button>
                  <Swap />
                </div>}
            {showOTCtrades && <div>
                <button className={classes.moveBack} onClick={moveBackHandler}> <img src='./img/direction.svg' alt='direction'/> Back</button>
                  <OTCtrades />
                </div>}
            {showManage && <div> <button className={classes.moveBack} onClick={moveBackHandler}> <img src='./img/direction.svg' alt='direction'/> Back</button>
               
                  <Manage />
                </div>}
                {showHodlVault && <div>
                <button className={classes.moveBack} onClick={moveBackHandler}> <img src='./img/direction.svg' alt='direction'/> Back</button>
                    <HodlVault />
                </div>}
        </div>
    )
}
export default Home;