import classes from './Main.module.css';
import { useState } from "react";
import './Main2.css';



import Notification from '../Notification/Notification';
import Home from '../Home/Home';
import Swap from '../Swap/Swap';
import OTCtrades from '../OTCTraders/OTCtrader';
import Manage from '../Manage/Manage';
import HodlVault from '../Hodl/HodlVault';
import History from '../History/History';
import Market from '../Markets/Market';
import Referrals from '../Referrals/Referral';




const Main = () => {
    const [showMore,setIsShowMore] = useState(false);
    const [toggleState, setToggleState] = useState(1);


    const toggleTab = (index) => {
      setToggleState(index);
    };

    const showMoreHandler = () => {
        setIsShowMore(!showMore )
    }
    return(
        <div className={classes.main}>
            <div className={classes.tab}>
                <div className={classes.logo}>
                    <img src='./img/logo.png'  alt='logo'/>
                </div>
                {/* connect section here ================================ */}
               <div className={classes.connect}>
                  <img className={classes.connect__Img} src='./img/connect.png' alt='Connect' />
                   <h2>Connect</h2>
                    <img className={classes.connect__share} src='./img/connectshare.png' alt='connect share'/>
               </div>

              {/* wallet section here ==================================== */}
              <div className={classes.wallet}>
                  <h2>Wallet</h2>
                  <img 
                     onClick={() => {
                        toggleTab(7);
                      }}    
                     src='./img/bell.png' alt='bell' />
              </div>

              {/* open tabs button ========================================= */}
              <div className={classes.tablinksTop}>
              <div
               onClick={() => {
                toggleTab(1);
              }}
               className={toggleState === 1 ? "tablinks active-tabs" : "tablinks"}>
                  <span><img src='./img/home.svg' alt='Home'/></span> 
                  <h2>Home</h2>
              </div>
              <div
               onClick={() => {
                toggleTab(2);
              }}
              className={toggleState === 2 ? "tablinks active-tabs" : "tablinks"}>
                  <span><img src='./img/setting.svg' alt='Home'/></span>
                  <h2>Manage</h2>
              </div>
              <div
               onClick={() => {
                toggleTab(3);
              }}
              className={toggleState === 3 ? "tablinks active-tabs" : "tablinks"}>
                  <span><img src='./img/swap.svg' alt='Home'/></span>
                  <h2>Swap</h2>
              </div>
              <div
               onClick={() => {
                toggleTab(4);
              }}
              className={toggleState === 4 ? "tablinks active-tabs" : "tablinks"}>
                  <span><img src='./img/signal.svg' alt='Home'/></span>
                  <h2>Markets</h2>
              </div>
              <div
               onClick={() => {
                toggleTab(5);
              }}
              className={toggleState === 5 ? "tablinks active-tabs" : "tablinks"}>
                   <span><img src='./img/trade.svg' alt='Home'/></span>
                  <h2>OTC trades</h2>
              </div>
              <div
                onClick={() => {
                    toggleTab(6);
                  }}
              className={toggleState === 6 ? "tablinks active-tabs" : "tablinks"}>
                   <span><img src='./img/history.svg' alt='Home'/></span>
                  <h2>History</h2>
              </div>
              <div
                onClick={() => {
                    toggleTab(10);
                  }}
              className={toggleState === 10 ? "tablinks active-tabs" : "tablinks"}>
                   <span><img src='./img/history.svg' alt='Home'/></span>
                  <h2>HODL Vault </h2>
              </div>
              
              <div>
                  {/* show more here======================================== */}
              {showMore && <div className="moreOption">
                    <a href='https://defitest.bubbleapps.io/version-test'>Github</a>
                 <button onClick={() => {
                        toggleTab(8);
                    }}
                       className={toggleState === 8 ? "tablinks active-tabs" : "tablinks"}>Markets</button> 
                   
                 <button onClick={() => {
                       toggleTab(9);
                    }}
                        className={toggleState === 9 ? "tablinks active-tabs" : "tablinks"}>Referrals</button> 
                    </div>}

              <div className={classes.more} onClick={showMoreHandler}>
                <span><img src='./img/more.svg' alt='Home'/></span>
                  <h2>More</h2>
              </div>
              </div>
              
             </div>
             


           </div>
             <div className={classes.tab_content}>
                    <div
                     className={toggleState === 1 ? "content active-content" : "content"}>
                   <Home />
                  </div>
                <div
                className={toggleState === 2 ? "content active-content" : "content"}>
                <Manage />
                </div>
                <div
                className={toggleState === 3 ? "content active-content" : "content"}>
                <Swap />
                </div>
                <div
                className={toggleState === 4 ? "content active-content" : "content"}>
                <Market />
                </div>
                <div
                className={toggleState === 5 ? "content active-content" : "content"}>
                <OTCtrades />
                </div>
                <div
                    className={toggleState === 6 ? "content active-content" : "content"}>
                    <History />
                </div>
                <div
                    className={toggleState === 7 ? "content active-content" : "content"}>
                    <Notification />
                </div>
                <div
                    className={toggleState === 8 ? "content active-content" : "content"}>
                    <Market />
                </div>
                <div
                    className={toggleState === 9 ? "content active-content" : "content"}>
                    <Referrals />
                </div>
                <div
                    className={toggleState === 10 ? "content active-content" : "content"}>
                    <HodlVault />
                </div>

            </div>
        </div>
    )
}
export default Main;