import classes from './Graph.module.css';
import { useState } from 'react';
import Swap from '../../Swap/Swap';
import OTCtrades from '../../OTCTraders/OTCtrader';
import { AreaChart, Area ,Tooltip, ResponsiveContainer } from 'recharts';



const data = [
    {
      name: 'Page A',
      uv: 4000,
      pv: 2400,
      amt: 2400,
    },
    {
      name: 'Page B',
      uv: 3000,
      pv: 1398,
      amt: 2210,
    },
    {
      name: 'Page C',
      uv: 2000,
      pv: 9800,
      amt: 2290,
    },
    {
      name: 'Page D',
      uv: 2780,
      pv: 3908,
      amt: 2000,
    },
    {
      name: 'Page E',
      uv: 1890,
      pv: 4800,
      amt: 2181,
    },
    {
      name: 'Page F',
      uv: 2390,
      pv: 3800,
      amt: 2500,
    },
    {
      name: 'Page G',
      uv: 3490,
      pv: 4300,
      amt: 2100,
    },
  ];
  



const Graph = () => {
     const [selectCurrency,setIsSelectCurrency] = useState("USD");
     const [selectMonth,setIsSelectMonth] = useState("MONTH1");
     const [showChart,setIsShowChart] = useState(true);
     const [showSwap,setIsShowSwap] = useState(false);
     const [showTransfer,setIsShowTransfer] = useState(false);
     const [showBuy,setIsShowBuy] = useState(false);



     const selectMonthHandler = (event) => {
        setIsSelectMonth(event.target.value);

     }

const swapHandler = () => {
    setIsShowChart(false);
    setIsShowSwap(true);
}
const transferHandler = () => {
    setIsShowTransfer(true);
    setIsShowChart(false);
}

const buyHandler = () => {
    setIsShowBuy(true);
    setIsShowChart(false);
}
     const selectCurrencyHandler = (event) => {
        setIsSelectCurrency(event.target.value);
     } 

   const currencySelected = () => {
       console.log(selectCurrency)
   }
   const monthSelected = () => {
       console.log(selectMonth);
   }

    return (
        <> 
        {showChart &&  
            <div className={classes.graphTop}>
           <div className={classes.currencyChange}>
                <span>BTC/{selectCurrency}</span>
                <div className={classes.currencyChangeDate}>
                    <select value={selectCurrency} onClick={currencySelected} onChange={selectCurrencyHandler}>
                        <option value="USD">USD</option>
                        <option value="EUR">EUR</option>
                        <option value="JPY">JPY</option>
                        <option value="GBP">GBP</option>
                        <option value="BTC">BTC</option>
                    </select>
                     <p>Chard Periods</p>
                     <select value={selectMonth} onClick={monthSelected} onChange={selectMonthHandler}>
                        <option value="DAY1">1 DAY</option>
                        <option value="WEEK1">1 WEEKS</option>
                        <option value="WEEKS2">2 WEEKS</option>
                        <option value="MONTH1">1 MONTH</option>
                        <option value="MONTH3">3 MONTHS</option>
                    </select>
                </div>
            </div>
            <div className={classes.cryptoChart}>
                <div className={classes.cryptoSelectedName}>
                    <img src='./img/bitcoin.png' alt='btc' />
                    <h2>BTC</h2>
                </div>
                <div className={classes.chartJs}>
                        <div className={classes.chartHeader}>
                            <div className={classes.left}>
                                <h1>$ 2,477.61 <span style={{color:"green"}}>(1.5 %)</span></h1>
                                <small>Last Updated : Just Now</small>
                                <br />
                                <small>Mkt Cap : $ 295.64 B</small>
                            </div>
                            <div className={classes.right}>
                                <small>USD Vol : 1.67 B</small>
                                <br />
                                <small>All Vol : 7.83 B</small>
                            </div>
                        </div>
                        <div className={classes.Chart}>
                        <ResponsiveContainer width="100%" height="100%">
              
                              <AreaChart
                              
                                width={500}
                                height={400}
                                data={data}
                                margin={{
                                    top: 40,
                                    right: 0,
                                    left: 0,
                                    bottom: 0,
                                }}
                                >
                                <Tooltip cursor={false}/>
                                <Area type="monotone" dataKey="date" stroke="#1BD88D" strokeWidth={3} fillOpacity={0.7} fill="#0D2F22" />
                                <Area type="monotone" dataKey="uv" stroke="#1BD88D" strokeWidth={3} fillOpacity={0.7} fill="#0D2F22" />
                            </AreaChart>
                         </ResponsiveContainer>
                        </div>
                        <div className={classes.chartBtn}>
                            <button onClick={transferHandler}>Transfer</button>
                            <button onClick={swapHandler}>Swap</button>
                            <button onClick={buyHandler}>Buy</button>
                        </div>
                    </div>
            </div>
            
           
        </div>
        } 
        {showSwap && <Swap />}
        {showTransfer && <Swap />}
        {showBuy && <OTCtrades />}
        </>
    )
}
export default Graph;