import { useState } from 'react/cjs/react.development';
import classes from './Market.module.css';
import Graph from './Graph.js/Graph';


const currency = [
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},
    {img: './img/bitcoin.png', name:"Bitcoin", nickName : "btc", price:"$ 63,000", hrsChange:"1.7 %"},

]

const Market = () => {
   const [inputSearch,setInputSearch] = useState("");
   const [showGraph,setIsShowGraph] = useState(false);
   const [showMarket,setIsShowMarket] = useState(true);

   const searchHandler = (event) => {
    setInputSearch(event.target.value);
   } 


   const backHandler = () => {
    setIsShowGraph(false);
    setIsShowMarket(true);
   }
  
   const showGraphHandler = () => {
       setIsShowGraph(true);
       setIsShowMarket(false);
   }

   const submitHandler = () => {
       console.log(inputSearch);
   }


    return(
        <div className={classes.marketTop}>
               {showMarket && <> <div className={classes.marketHeader}>
                    <h1>Markets</h1>
                    <div className={classes.formSearch}>
                        <form>
                            <input onChange={searchHandler} value={inputSearch} type="text" placeholder="Search" />
                            <img onClick={submitHandler} src='./img/search.svg' placeholder='Search' alt='Search' />
                        </form>
                    </div>
                </div>
                <div className={classes.marketCoin}>
                    <div className={classes.marketHeading}>
                        <span className={classes.token}>
                            <h3>Token</h3>
                        </span>
                        <span className={classes.price}>
                            <h3>Price &nbsp; <img src='./img/down.svg' alt='Direction down' /></h3>
                        </span>
                        <span className={classes.changeHrs}>
                            <h3>24 hrs Change  &nbsp; <img src='./img/down.svg' alt='Direction down' /></h3>
                        </span>
                        <span className={classes.empty}>
                            
                        </span>
                    </div>

                 
                        {currency.map((crypto) => (
                            <div className={classes.cryptoName} onClick={showGraphHandler}>
                            
                            <div className={classes.cryptoIcon}>
                                <img src={crypto.img} alt='bitcoin icon'/>
                                <span className={classes.cryptoNam}>
                                    <h4>{crypto.name}</h4>
                                    <p>{crypto.nickName}</p>
                                </span>
                            </div>
                            <div className={classes.cryptoPrice}>
                                <h4>{crypto.price}</h4>
                            </div>
                            <div className={classes.cryptoHrsChange}>
                                <h4>{crypto.hrsChange}</h4>
                            </div>
                            <div className={classes.star}>
                                <img src='./img/star.svg' alt='Star Icon'/>
                            </div>
                            </div>
                        ))}
                
                </div>
                </>
                }

                {showGraph && <>
                <button className={classes.moveBack} onClick={backHandler}><img src='./img/direction.svg' alt='direction'/> Back</button>
                    <Graph />
                </> }
        </div>
    )
}

export default Market;