import { useState } from 'react/cjs/react.development';
import classes from './Referral.module.css';
const Referrals = () => {
    const [getRefCode,setIsgetRefCode] = useState("")

    const getRefCodeHandler = (event) => {
        setIsgetRefCode(event.target.value);
    }
    return(
       <div className={classes.referralTop}>
           <div className={classes.row}>
              <div className={classes.left}>
                  <h5>Your Referral Code:</h5>
                  <span>
                      <h2>LUNejpe5</h2>
                      <input type='checkbox' />
                  </span>
                  <p>Press Confirm to activate Your Ref-code</p>
              </div>
              <div className={classes.right}>
                  <h5>Enter Referral Code:</h5>
                  <span>
                      <input type="text" value={getRefCode} onChange={getRefCodeHandler} placeholder='Ref Code' />
                     <img src='./img/check.svg' alt='Check' />
                  </span>
                  <p>Enter Ref Ref-code To Activate Rewards</p>
              </div>
           </div>
           <h3>To generate new Ref-code refresh the page</h3>
       </div>
    )
}

export default Referrals;