import classes from './Manage.module.css';
import './Manage.css';
import { useState } from 'react';


import Swap from '../Swap/Swap';

const crypto = [
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    },
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    },
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    },
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    },
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    },
    {
        img : './img/ohm.png',
        price:'$ 200',
        range : "$ 1.7",

    }
]


const Manage = () => {
    const [toggleNftState, setToggleNftState] = useState(1);
    const [addNft,setIsAddNst] = useState(false);
    const [showSearch,setIsShowSearch] = useState(false);
    const [hideNft,setIsHideNft] = useState(true);
    const [showSwap,setIsShowSwap] = useState(false);
    const [showReceiveFunds,setIsShowReceiveFunds] = useState(false);
    const [hideSecondButton,setIsHideSecondButton] =  useState(false);



    const showReceiveFundsHandler = () => {
        setIsShowReceiveFunds(true);
    }

    const hideReceiveFunds = () => {
        setIsShowReceiveFunds(false);
    }

    const showSwapHandler = () => {
        setIsShowSwap(true);
        setIsHideSecondButton(true);
    }
const moveSwapBackHandler = () => {
    setIsShowSwap(false);
}

    const showNftAddHandler = () => {
        setIsAddNst(true);
    }

    const hideNftAddHandler = () => {
        setIsAddNst(false);

    }
    const hideWalletAssetsHandler = () => {
        setIsHideNft(true);
    }
    const hideNftHandler = () => {
        setIsHideNft(false);
    } 

    const toggleTab = (index) => {
        setToggleNftState(index);
        setIsShowSearch(true);
    };
    return(
        <div className={classes.manageTop}>
           {!showSwap && <><h1>Summary</h1>
            <div className={classes.manageTopHeader}>
                <div className={classes.left}>
                    <div className={classes.nftAssets}>
                        <div className={classes.nft}>
                            <div 
                                onClick={() => {
                                 toggleTab(1);
                                 setIsShowSearch(false);
                                 setIsHideNft(true);
                                }} 
                               
                                className={toggleNftState === 1 ? "nftTablinks active-nfttabs" : "nftTablinks"}  
                            >NFT</div>

                            <div
                        
                                onClick={() => {
                                 toggleTab(2);
                                 setIsHideNft(true);
                                 
                              }}
                              className={toggleNftState === 2 ? "nftTablinks active-nfttabs" : "nftTablinks"}
                              >Assets</div>
                        </div>
                            
                         <div className={classes.BSC}>
                            <img src='./img/bsc.png' alt='BSC' />
                            <h3>BNB</h3>   
                          </div>   
                          <div className={classes.ETH}>
                            <img src='./img/connect.png' alt='BSC' />
                            <h3>BNB</h3>   
                          </div>   
                    </div>
                </div>
                <div className={classes.right}>
                    <img onClick={hideNftHandler} title='My wallet Assets' src='./img/storage.svg' alt="swap" />
                    <img src='./img/swapS.svg' title='transfer' onClick={showSwapHandler} alt="swap" />
                    <img src='./img/down.svg' title='Receive funds'  alt="swap" />
                    <img src='./img/plus.svg' title='Add custom Tokens' onClick={showReceiveFundsHandler}  alt="swap" />
                </div>
            </div>
            <div className={classes.manageMainContent}>
               <div className={classes.totalNetworthHeader}>
                 <div className={classes.totalNetworth}>
                    <p>Total Networth</p>
                    <p>*************</p>
                 </div>
                 {showSearch &&
                 <div className={classes.searchCrypto}>
                    <div>
                            {hideNft && 
                            <>
                                <p>Custom token</p>
                                <img src='./img/star.svg' alt='Star' />
                                </>
                            }
                            {!hideNft &&
                             <>
                                <p>Your Wallet Assets Only</p>
                                <img src='./img/close.svg' onClick={hideWalletAssetsHandler}  alt='Star' />
                             </>
                            }

                    </div>
                    <form>
                    <input type="text" placeholder='Search' />
                    </form>
                 </div>
                 }
               </div>
                <div 
                  className={toggleNftState === 1 ? "contentNft activeNft-content" : "contentNft"}
                >
                   {addNft && 
                    <div className='addNftForm' >
                        <form>
                            <img src='./img/close.svg' alt='close' onClick={hideNftAddHandler} />
                            <h3>Add Costum NFT on ETH</h3>
                            <label htmlFor='tokenAddress'>Token Address</label>
                            <input type='text' id='tokenAddress' placeholder='Token Address'/>
                            <br/>
                            <label htmlFor='tokenId'>Token ID</label>
                            <input type='text' id='tokenId' placeholder='Token ID'/>
                            <br/>
                            <input type='submit' value='Load Data'/>
                        </form>
                   </div>
                   }
                     {hideNft &&
                      <>
                          <button className='addNft' onClick={showNftAddHandler}>Add NFT</button>
                      </>
                    }
                     {!hideNft && <> <div className={classes.AssetTop}>
                        <div className={classes.token}>
                           <h3>Token</h3> 
                        </div>
                        <div className={classes.hrsChange}>
                            <h3>Price</h3>
                        </div>
                        <div className={classes.balance}>
                            <h3>Balance</h3>
                        </div>
                        <div className={classes.usdbalance}>
                            <h3>USD balance</h3>
                        </div>
                        <div className={classes.empty}>

                        </div>
                    </div>
                   {crypto.map((crypto)=> (
                        // you can loop through here
                        <span></span>
                   ))}
                   </>
                   }
                </div>
                <div 
                 className={toggleNftState === 2 ? "contentNft activeNft-content" : "contentNft"}>
                   {hideNft && <> <div className={classes.AssetTop}>
                        <div className={classes.token}>
                           <h3>Token</h3> 
                        </div>
                        <div className={classes.hrsChange}>
                            <h3>24 Hrs / Change</h3>
                        </div>
                        <div className={classes.balance}>
                            <h3>Balance</h3>
                        </div>
                        <div className={classes.usdbalance}>
                            <h3>USD balance</h3>
                        </div>
                        <div className={classes.empty}>

                        </div>
                    </div>
                   {crypto.map((crypto)=> (
                        <div className={classes.assetCoin}>
                        <div className={classes.tokenName}>
                            <img src={crypto.img} alt='ohm' />
                            <h3>OHM</h3> 
                         </div>
                         <div className={classes.hrsChangePrice}>
                             <img src='./img/directiondown.svg' alt='down'/>
                             <div className={classes.priceChange}>
                                 <h3>{crypto.price}</h3>
                                 <p style={{color:"red"}}>{crypto.range}</p>
                             </div>
                         </div>
                         <div className={classes.balanceCoin}>
                             <h3>******</h3>
                         </div>
                         <div className={classes.usdbalanceCoin}>
                             <h3>$ 0</h3>
                         </div>
                         <div className={classes.emptyShare}>
                             <img src='./img/swapS.svg' onClick={showSwapHandler} title='transfer' alt='Swap' />
                             <img src='./img/swap1.svg' onClick={showSwapHandler} title='Swap' alt='Swap' />
                         </div>
                     </div>
                   ))}
                   </>
                   }

                    {!hideNft && <> <div className={classes.AssetTop}>
                        <div className={classes.token}>
                           <h3>Token</h3> 
                        </div>
                        <div className={classes.hrsChange}>
                            <h3>Price</h3>
                        </div>
                        <div className={classes.balance}>
                            <h3>Balance</h3>
                        </div>
                        <div className={classes.usdbalance}>
                            <h3>USD balance</h3>
                        </div>
                        <div className={classes.empty}>

                        </div>
                    </div>
                   {crypto.map((crypto)=> (
                        // you can loop through here
                        <span></span>
                   ))}
                   </>
                   }
                </div>
            </div>
            </>
            }
            {showSwap && 
            <>
            {!hideSecondButton && <button className={classes.moveSwapBack} onClick={moveSwapBackHandler}> <img src='./img/direction.svg' alt='direction'/> Back</button>
                          }              <Swap />
            </>
            }


            {/* all form receive fund modal and add  ====================*/}
            {showReceiveFunds && <div className={classes.receiveFunds} >
                <div>
                    <img  src='./img/close.svg' onClick={hideReceiveFunds} alt='close' />
                   <h2>Add Custom Tokens</h2>
                   <p>ERC-20 Address</p>
                   <form>
                       <label htmlFor='tokenContract'>Token Contract Address</label> 
                       <input type='text' id='tokenContract' placeholder='ERC-20 contract Address' />
                       <br/>
                       <label htmlFor='tokenName'>Token Name</label> 
                       <input type='text' id='tokenName' />
                       <br/>
                       <label htmlFor='tokenSymbol'>Token Symbol</label> 
                       <input type='text' id='tokenSymbol' />
                       <br/>
                       <input type='submit' value='Import' />
                   </form>
                </div>
            </div>
            }
        </div>
    )
}
export default Manage;